html, body{
  width: 100%;
  height: 100%;
  position: relative;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans',
    sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-stroke: 0px!important;-webkit-font-smoothing: antialiased !important;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App, #root{
  height: 100%;
  width: 100%;
}

h1, h2{
  margin: 0;
}


h1{
  font-family: 'Bellefair', serif;
  font-weight: 400;
  font-size: 110px;
  letter-spacing: -6px;
  text-align: center;
  color: #000;
  margin-top: 160px;
}

h2{
  color: #484848;
  font-weight: 300;
  font-size: 24px;
  margin-top: -10px;
}

h5{
  font-weight: 300;
  font-size: 18px;
}

a{
  font-weight: 700;
  color: #484848;
  border-bottom: 2px solid #484848;
  text-decoration: none;
  transition: .3s ease-in-out;
}

a:hover{
  color: #e0e0e0;
  border-bottom: 2px solid #e0e0e0;
}

.h-100{
  height: 100%;
  display: block;
  width: 100%;
}

p{
  font-weight: 300;
  line-height: 150%;
  width: 50%;
  margin: 0 auto;
  font-size: 18px;
  text-align: left;
  position: relative;
  margin-bottom: 50px;
  background: #fff;z-index: 999;
  padding-left: 25%;
  padding-right: 25%;
  padding-bottom: 50px;

}


.frame{
  width: 100%;
  height: 600px;
  z-index: -9999;
  position: relative;
}

footer{
  width: 60%;
  margin: 0 auto;
  line-height: 150%;
  margin-bottom: 50px;
}

.w-40{
  margin-left: 10%;
  width: 80%;
  z-index: -9;
  margin-top: -100px;
}

.right{
  border-left: 0px;

}

button{
  display: inline-block;
  padding: 15px;
  font-family: "Bellefair", serif;
  font-size: 25px;
  background: white;
  border: 0px solid transparent;
  padding-left: 30px;
  padding-right: 30px;
  margin: 15px;
}

.selected{
  background: #ebebeb;
}

.left{
  border-right: 0px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.redbar{
  height: 80px;
  display: inline-block;
  background: rgb(142, 30, 40);
  text-align: left;
  color: white;
  line-height: 80px;
  padding: 10px;
  background:white;
}

.blackline{
  width: 3px;
  height: 80px;
  display: inline-block;
  background: white;
  color: white;
  line-height: 80px;
  padding-top: 10px;
  padding-bottom: 10px;


}
.greenbar{
  height: 80px;
  display: inline-block;
  background: rgb(64, 104, 55);
  text-align: right;
  color: white;
  line-height: 80px;
  padding: 10px;
  background:white;

}

.p-bottom-10{
  padding-bottom:10%;
}

.redbar-25{
  width: 93px;
  background: rgb(142, 30, 40);

}

.greenbar-300{
  width: 557px;
  background: rgb(64, 104, 55);

}

.redbar-50{
  width: 100px;
  background: rgb(142, 30, 40);

}

.greenbar-275{
  background: rgb(64, 104, 55);

  width: 550px;
}

.redbar-151{
  width: 302px;
  background: rgb(142, 30, 40);
}

.greenbar-174{
  width: 348px;
  background: rgb(64, 104, 55);
}

.text{
  margin-top: 15px;
}

.liam{
  width: 60%;
  margin-top: 40px;
  margin-bottom: 80px;
}


.lucy{
  width: 60%;
  margin-top: 0px;
  margin-bottom: 80px;
}


.p1{
  margin-top: -140px;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

